function Square(props) {
	if (props.valuecontains("Rock")) {
    return (
      <button disabled={props.value.contains("true")} className="square" onClick={props.onClick}>
        {<img src = 'https://goodday451999.github.io/Rock-Paper-Scissors-Neo/images/stone.png' width="100" height="100"/>}
      </button>
    );
  } else if (props.value.contains("Paper")) {
     return (
      <button disabled={props.value.contains("true")} className="square" onClick={props.onClick}>
        {<img src = 'https://hacktoberfest.lingonsaft.com/images/paper.png' width="100" height="100"/>}
      </button>
     );
  } else if (props.value.contains("Scissors")) {
    return (
      <button disabled={props.value.contains("true")} className="square" onClick={props.onClick}>
        {<img src = 'https://hacktoberfest.lingonsaft.com/images/scissors.png' width="100" height="100"/>}
      </button>
    );
  }
}


class Game extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
	  player: 0,
      computer: 0,
      results: ["n_nothing","nothing"],
	  //isDisabled: false,
      isGame : true,
	  movesIsDisabled: ["Rock", "Paper", "Scissors", "false"]
    };

    this.enableGamePage  = this.enableGamePage.bind(this);
	this.switchPage  = this.switchPage.bind(this);
  }
  
  handleClick(i) {
    const moves = ["Rock", "Paper", "Scissors"];
    const move = moves[Math.floor(Math.random() * Math.floor(3))];
    const result = i + move;
    
    if (i.split("_")[1] == move) {
      this.setState({results: [i,move]})
    }else if (result.includes('RockScissors') || result.includes('ScissorsPaper') || result.includes('PaperRock')) { // if it's the players turn
      this.setState({
        player: this.state.player+1,
        results: [i,move],
    });
    } else { // if it's the computers turn
      this.setState({
        computer: this.state.computer+1,
        results: [i,move],
      });
    }
  }
  
  renderSquare(i) {
		return (
			<Square
				value={i}
				onClick={() => this.handleClick(i)}
			/>
		);
	}
  
  
  enableGamePage() {
    this.setState({
		isGame : false,
    });
  }
  
  switchPage() {
	this.setState({
		isGame : true,
    });
  }
  
  render() {
    const game = (
       <div className="game">
        <div className="game-board">
          <div>
			<div>{"Player score: " + this.state.player}</div>
			<div>{"Computer score: " + this.state.computer}</div>
			<div>{"You played " + this.state.results[0].split("_")[1] + ". Computer played " + this.state.results[1] + "."}</div>
				<div className="board-row">
				  {this.renderSquare(this.state.movesIsDisabled[3].concat("_",this.state.movesIsDisabled[0]))}
				  {this.renderSquare(this.state.movesIsDisabled[3].concat("_",this.state.movesIsDisabled[1]))}
				  {this.renderSquare(this.state.movesIsDisabled[3].concat("_",this.state.movesIsDisabled[2]))}
				</div>
			</div>
        </div>
        <div>
            <button onClick={this.enableGamePage} className="loginbutton">Next</button>
          </div>
      </div>
    );
	
	const alt = (
		   <div className="game2">
		   <div>
            <button onClick={this.switchPage} className="loginbutton">Next</button>
          </div>
		  </div>
		);
    
    return (
      <div>
        { this.state.isGame ? game : alt }
      </div>
    );
  }
}



ReactDOM.render(
  <Game />,
  document.getElementById('root'),
);

